.contract-management-container {
  max-width: 1200px;
  margin: 100px auto;
  text-align: left;
  margin-top: 120px;
}

/* 제목과 설명을 한 줄 */
.title-container {
  display: flex;
  align-items: baseline;
  gap: 15px;
  margin-bottom: 20px;
}

.title {
  font-size: 24px;
  font-weight: bold;
}

.description {
  font-size: 16px;
  color: #666;
}

.table-header {
  width: 100%;
  border-radius: 12px;
  overflow: hidden;
  border: 1px solid #ddd;
  background: #fff;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 15px;
  height: 30px;
  justify-content: space-around;
  align-items: center;
}

/* 내부 요소들 균일한 크기로 만들기 */
.table-header span {
  flex: 1;
  text-align: center;
}

.table-header,
.table-row {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 12px 20px;
  border-bottom: none !important;
}

.table-row {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 12px 20px;
  border-bottom: 1px solid #ddd;
}

.table-row span {
  flex: 1;
  text-align: center;
  font-weight: 600;
}

.subtitle {
  margin-top: 70px;
  font-size: 24px;
  font-weight: bold;
  text-align: left;
  margin-bottom: 35px;
}

.table-header {
  font-weight: bold;
  background: #f5f5f5;
  color: #686868;
}

/* 닫기기 X 버튼 */
.close-icon {
  position: absolute;
  top: 20px;
  right: 20px;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 40px;
  color: black;
  transition: color 0.3s ease;
  margin-top: 90px;
  margin-right: 40px;
}

.close-icon:hover {
  color: #00b8ff;
}
