.user-type-container {
  text-align: center;
  padding: 20px;
  margin-top: 240px;
}

.user-type-buttons {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.user-type-button {
  padding: 10px 20px;
  font-size: 16px;
  border: 1px solid #ccc;
  background-color: #f0f0f0;
  cursor: pointer;
  border-radius: 5px;
}

.user-type-button.selected {
  background-color: #007bff;
  color: white;
  font-weight: bold;
}
